.o-title-stripe {

  padding: 1em;
  margin-top: 0;
  margin-bottom: 3rem;

  text-align: center;
  font-size: $font-size-large;
  font-family: $base-font-family;
  font-weight: 100;
  font-style: normal;
  line-height: 1.2;
  text-transform: uppercase;

  color: $color-white;
  background: $color-blue-light;

  @media( min-width: $mobile ){
    font-size: $font-size-large-2;
  }

}
