.u-vertical-space {
  padding-top: $margin-height;
  padding-bottom: $margin-height;
}

.u-vertical-space-top {

  &-null {
  padding-top: 0;
  }

  &-half {
  padding-top: $margin-height / 2;
  }

  &-normal {
  padding-top: $margin-height;
  }

  &-double {
  padding-top: $margin-height * 2 ;
  }

}

.u-vertical-space-bottom {

  &-null {
  padding-bottom: 0;
  }

  &-half {
  padding-bottom: $margin-height / 2;
  }

  &-normal {
  padding-bottom: $margin-height;
  }

  &-double {
  padding-bottom: $margin-height * 2 ;
  }

}
