


// Layout and breakpoints
$main-container-width: 1040px;
$main-container-width-wide: 1150px;
$main-container-width-ultrawide: 1285px;
$main-container-padding: 20px;

// Breakpoints
$mobile: 480px;
$tablet: 769px;
$desktop: 1025px;
$wide: 1367px;
$ultra-wide: 1690px;

// Colors
$color-brown: #b6a583;
// $color-brown: #A59269; // Acesible
$color-brown-light: #cabda5;
$color-gray: #bbb;
$color-white: #fff;
$color-black: #000;
$color-blue: #284755;
$color-blue-light: #1f437a;

$color-primary: $color-blue;
$color-calltoaction: $color-blue-light;
$color-background: #fff;
$color-links: #0563c1;

$color-primary-text: $color-blue;
$color-secondary-text: $color-white;


// Fonts
$base-font-family: 'Montserrat', sans-serif, 'Geneva', Helvetica, ;
$heading-font-family: Georgia, 'Droid Serif', 'Lucida Bright', 'Lucida Serif', Lucida, 'DejaVu Serif', 'Bitstream Vera Serif', 'Liberation Serif', serif;

$font-family-base: $base-font-family;
$font-family-title: $heading-font-family;

// Font Weights
$light: 300;
$normal: 400;
$semibold: 600;
$bold: 700;

// Font sizes
//$base-font-size: 1em;

$base-font-size: 16px;
$font-size-base: $base-font-size;

$font-size-small: 0.75rem;       //12px
$font-size-med: 1rem;            //16px
$font-size-med-1: 1.125rem;      //18px
$font-size-med-2: 1.25rem;       //20px
$font-size-large: 1.5rem;        //24px
$font-size-large-1: 2rem;        //32px
$font-size-large-2: 2.5rem;      //40px
$font-size-large-3: 2.875rem;    //46px
$font-size-large-4: 3.25rem;     //46px
$font-size-huge: 5.375rem;       //86px
$font-size-huge-1: 8rem;         //128px

// Animations
$animation-fast: .4s ease;
$animation-normal: 1s ease;
$animation-slow: 1.5s ease;

// Header height
$header-height: 92px;
$header-height-tablet: 124px;
$header-height-desktop: 124px;
$header-height-wide: 124px;

// Main-nav height
$main-nav-height: 48px;
$main-nav-height-tablet: 55px;
$main-nav-height-desktop: 64px;

// Breadcrumb
$breadcrumb-height: 30px;

// Secondary-nav (degree navigation) height
$secondary-nav-height: 35px;
// $secondary-nav-height-tablet: 55px;
// $secondary-nav-height-desktop: 64px;
$secondary-nav-tab-radius: 8px;
$secondary-nav-stacked-increment: 1.2;

// Margins width
$margin-width: 26px;
$margin-width-tablet: 30px;
$margin-width-desktop: 40px;
$margin-width-wide: 40px;

// Padding width
$padding-width: 50px;

// Margins height
$margin-height: 35px;

// Title stripes config
$title-stripe-distance: 2.4em;
$title-stripe-spacing: 0.4em;
$title-stripe-opacity: .7;
