/*
Core Stuff

Importado de los estilos generados por defecto de Grav.
*/
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-size: 1rem;
  margin: 0;
  line-height: 1.7;
  color: $color-primary-text;
  background: #fff;
}

a {
  color: inherit;

  nav & {
    color: inherit;
    text-decoration: none;
  }
}

a:hover {

  .u-bg.is-blue & {
    color: $color-secondary-text;
  }
}

pre {
  background: #F0F0F0;
  margin: 1rem 0;
  border-radius: 2px;
}

blockquote {
  border-left: 10px solid #eee;
  margin: 0;
  padding: 0 2rem;
}

li {
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

p {
	margin: 0 0 1em 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

hr {
  color: $color-primary;

  .u-bg.is-blue & {
    color: $color-secondary-text;
  }

  .u-bg.is-brown & {
    color: $color-secondary-text;
  }
}

.rtecenter,
.u-text-center {
  text-align: center;
}

li a.active {
  color: inherit;
}


